import {WinJSDec, numError} from './APP/config';

var name = 'DEVICE_VIBRATE';
var DEVICE_VIBRATE = (time = 200) => {
  if (typeof time === 'number') {
    WinJSDec(_ => {
      window.WinJSBridge.call('device', 'vibrate', {time: time}, function (res) {
      });
    });
  } else {
    throw new TypeError(`[${name}] ${numError}`);
  }
};
export default DEVICE_VIBRATE;